import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IncomingAndOutgoingComponent } from './incomingAndOutgoing.component';

const routes: Routes = [
    {
        path: '',
        component: IncomingAndOutgoingComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class IncomingAndOutgoingRoutingModule {}
