import { Component, OnInit } from '@angular/core';
import { DashBoardService } from 'src/app/demo/Services/dashBoard/dashBoard.service';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { IncomingAndOutgoingRe } from 'src/app/demo/_model/incomingAndOutgoing';
import { TeacherHoursService } from 'src/app/demo/Services/teacherHours/teacherHours.service';

@Component({
    selector: 'app-incomingAndOutgoing',
    templateUrl: './incomingAndOutgoing.component.html',
    styleUrls: ['./incomingAndOutgoing.component.scss']
})
export class IncomingAndOutgoingComponent implements OnInit {
  IncomingAndOutgoing: IncomingAndOutgoingRe
 
  constructor(public teacherHoursService: TeacherHoursService,public AccountService :AccountService ) {}

  ngOnInit() {
   
    this.teacherHoursService.IncomingAndOutgoing().subscribe(res=>{
      this.IncomingAndOutgoing = res;
    })


  }
}

